import {ScrollLock} from '../utils/scroll-lock';

const menuBtn = document.querySelector('.mob-menu__toggle');
const menuBody = document.querySelector('.main-nav');
const main = document.querySelector('.header__bottom');
const scroll = new ScrollLock();

function isActive() {
  if (!menuBtn) {
    return;
  }

  menuBtn.addEventListener('click', () => {
    if (!menuBody.classList.contains('is-active')) {
      menuBody.classList.add('is-active');
      menuBtn.classList.add('is-active');
      main.classList.add('overlay');
      menuBtn.setAttribute('aria-pressed', 'true');
      scroll.disableScrolling();
    } else {
      menuBody.classList.remove('is-active');
      menuBtn.classList.remove('is-active');
      main.classList.remove('overlay');
      menuBtn.setAttribute('aria-pressed', 'false');
      scroll.enableScrolling();
    }
  });
}

export {isActive};
