const pageHeaderElement = document.querySelector('.header');

function pageHeaderHeight() {
  if (!pageHeaderElement) {
    return;
  }
  let windowWidth = window.innerWidth;
  const definePageHeaderHeight = () => {
    document.documentElement.style.setProperty('--page-header-height', `size(${ pageHeaderElement.offsetHeight }px)`);
  };

  definePageHeaderHeight();

  window.addEventListener('resize', () => {
    if (windowWidth !== window.innerWidth) {
      windowWidth = window.innerWidth;

      definePageHeaderHeight();
    }
  });
}

export {pageHeaderHeight};
