import {a11y} from './sliders-constants';

const sliders = document.querySelectorAll('.slider');

const initIntroSlider = () => {

  if (!sliders.length) {
    return;
  }

  sliders.forEach((slider) => {
    const container = slider.querySelector('[data-slider]');
    // eslint-disable-next-line no-new, no-undef
    new window.Swiper(container, {
      a11y,
      slidesPerView: 'auto',
      loop: true,
      preloadImages: false,
      lazy: true,
      observer: true,
      effect: 'fade',
      fadeEffect: {
        crossFade: true,
      },
      navigation: {
        nextEl: container.querySelector('.slider__btn--prev'),
        prevEl: container.querySelector('.slider__btn--next'),
      },
    });
  });
};

export {initIntroSlider};
